/* V. Sprite
 --------------------------------------- */ 

/* 1. Sprite:svg 
 -------------------------*/
// Для добавления svg-спрайтов: необходимо только задать нужный класс а html.
// например     i.svg-icons.svg-icons-dims
@import "../../../tmp/sass/sprite-svg.scss"

/* 2. Sprite:png  
 -------------------------*/
// sass-свойства спрайтов не добавлены по умочанию, свойство каждого спрайта нужно добавлять отдельно.
// Для добавления png-спрайтов:
//      - добавить в html разметку
//                                  div.sprite-class либо 
//                                  i.sprite-class
//      - добавить в sass-файл свойство 
//                                   .icon-home 
//                                          @include sprite($icon-email)
//                                  
// 

@import "../../../tmp/sass/sprite-png.sass"

// Sprite:png styles
// .icon-email 
