/* Modals
  -------------------------*/
.modal 
  padding: 1.5rem
  // display: -webkit-box
  // display: -ms-flexbox
  // display: flex
  display: none
  -webkit-box-align: center
  -ms-flex-align: center
  align-items: center
  width: 100vw
  height: 100%
  position: fixed
  left: 0
  top: 0
  right: 0
  bottom: 0
  z-index: 999
  opacity: 0
  visibility: hidden
  overflow: auto

.modal__content 
  margin: auto
  padding: 8rem 5rem
  display: -webkit-box
  display: -ms-flexbox
  display: flex
  -webkit-box-orient: vertical
  -webkit-box-direction: normal
  -ms-flex-direction: column
  flex-direction: column
  width: 52rem
  position: relative
  z-index: 1001
  border-radius: 1rem
  background-color: #e3e8ed
  text-align: center
  opacity: 0

.modal__header 
  margin-bottom: 4rem
  position: relative

.modal__header p 
  margin: 0 auto
  font-size: 2.2rem

.modal__body 
  -webkit-box-flex: 1
  -ms-flex-positive: 1
  flex-grow: 1
  text-align: justify
  overflow: visible

.modal__footer 
  font-size: 1.2rem

.modal__footer p 
  text-align: center

.modal__footer a 
  text-decoration: underline

.modal__footer a:hover 
  text-decoration: none

.modal__close 
  padding: 0
  width: 2rem
  height: 2rem
  position: absolute
  top: -8rem
  right: -10rem
  border: none
  background: none

.modal-close__item 
  display: block
  width: 2.8rem
  height: 5px
  position: absolute
  left: -0.4rem
  top: 50%
  background-color: #ffffff
  -webkit-transition: background-color 0.3s
  -o-transition: background-color 0.3s
  transition: background-color 0.3s


.modal-close__item:first-child 
  -webkit-transform: rotate(45deg)
  -ms-transform: rotate(45deg)
  transform: rotate(45deg)

.modal-close__item:last-child 
  -webkit-transform: rotate(-45deg)
  -ms-transform: rotate(-45deg)
  transform: rotate(-45deg)

.modal-close:hover .modal-close__item 
  background-color: #cccccc

.modal__overlay 
  display: block
  width: 100%
  height: 100%
  position: fixed
  top: 0
  left: 0
  z-index: 1000
  background-color: rgba(0, 0, 0, 0.84)
  opacity: 0
  visibility: hidden

.modal.opened 
  -webkit-animation: fadeIn 0.5s ease-out forwards
  animation: fadeIn 0.5s ease-out forwards

.modal.opened .modal__content 
  -webkit-animation: slideDownIn 0.3s ease-out forwards
  animation: slideDownIn 0.3s ease-out forwards

.modal.opened .modal__overlay 
  -webkit-animation: fadeIn 0.5s ease forwards
  animation: fadeIn 0.5s ease forwards

.modal.closed 
  -webkit-animation: fadeOut 0.5s ease-in forwards
  animation: fadeOut 0.5s ease-in forwards

.modal.closed .modal__content 
  -webkit-animation: slideUpOut 0.3s ease-in forwards
  animation: slideUpOut 0.3s ease-in forwards

.modal.closed .modal__overlay 
  -webkit-animation: fadeOut 0.5s ease-in forwards
  animation: fadeOut 0.5s ease-in forwards

