.thanks 
  padding: 3rem
  display: -webkit-box
  display: -ms-flexbox
  display: flex
  -webkit-box-pack: center
  -ms-flex-pack: center
  justify-content: center
  -webkit-box-align: center
  -ms-flex-align: center
  align-items: center
  width: 42rem
  position: fixed
  left: 50%
  top: 50%
  -webkit-transform: translate(-50%, -50%)
  -ms-transform: translate(-50%, -50%)
  transform: translate(-50%, -50%)
  text-align: center
  border-radius: 1rem
  -webkit-box-shadow: 0 0 30px rgba(0, 0, 0, 0.5)
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.5)
  background: #e3e8ed
  opacity: 0
  z-index: -1
  visibility: hidden
  -webkit-transition: opacity 0.5s
  -o-transition: opacity 0.5s
  transition: opacity 0.5s

.thanks h3 
  font-weight: 500

.thanks h3 span 
  font-weight: 700
  font-size: 2.6rem

.thanks span 
  display: block
  margin-bottom: 2rem

.thanks__close 
  position: absolute
  top: 1rem
  right: 1rem
  width: 2.4rem
  height: 2.4rem
  -webkit-transition: -webkit-transform 0.2s
  transition: -webkit-transform 0.2s
  -o-transition: transform 0.2s
  transition: transform 0.2s
  transition: transform 0.2s, -webkit-transform 0.2s

.thanks__close::before, .thanks__close::after 
  display: block
  position: absolute
  top: 50%
  content: ""
  width: 100%
  height: 0.3rem
  border-radius: 10px
  background: #4b4b4b
  -webkit-transition: background-color 0.2s
  -o-transition: background-color 0.2s
  transition: background-color 0.2s

.thanks__close::before 
  -webkit-transform: rotate(45deg)
  -ms-transform: rotate(45deg)
  transform: rotate(45deg)

.thanks__close::after 
  -webkit-transform: rotate(-45deg)
  -ms-transform: rotate(-45deg)
  transform: rotate(-45deg)

.thanks__close:hover 
  -webkit-transform: scale(1.05)
  -ms-transform: scale(1.05)
  transform: scale(1.05)

.thanks__close:hover .thanks__close::before,
.thanks__close:hover .thanks__close::after 
  background: #191919

.thanks.active 
  z-index: 99999
  visibility: visible
  opacity: 1
  -webkit-animation: fadeIn 1.5s ease forwards, fadeOut 1.5s ease forwards 5s
  animation: fadeIn 1.5s ease forwards, fadeOut 1.5s ease forwards 5s


