/* 
  ANIMATIONS
  ------------------------------ */
@-webkit-keyframes fadeIn 
  from 
    visibility: hidden
    opacity: 0
  
  to 
    visibility: visible
    opacity: 1
  

@keyframes fadeIn 
  from 
    visibility: hidden
    opacity: 0
  
  to 
    visibility: visible
    opacity: 1
  

@-webkit-keyframes fadeOut 
  from 
    visibility: visible
    opacity: 1
  
  to 
    visibility: hidden
    opacity: 0
  

@keyframes fadeOut 
  from 
    visibility: visible
    opacity: 1
  
  to 
    visibility: hidden
    opacity: 0
  

@-webkit-keyframes slideDownIn 
  from 
    opacity: 0
    visibility: hidden
    -webkit-transform: translateY(-10%)
    transform: translateY(-10%)
  
  to 
    opacity: 1
    visibility: visible
    -webkit-transform: translateY(0%)
    transform: translateY(0%)
  

@keyframes slideDownIn 
  from 
    opacity: 0
    visibility: hidden
    -webkit-transform: translateY(-10%)
    transform: translateY(-10%)
  
  to 
    opacity: 1
    visibility: visible
    -webkit-transform: translateY(0%)
    transform: translateY(0%)
  

@-webkit-keyframes slideDownOut 
  from 
    opacity: 1
    visibility: visible
    -webkit-transform: translateY(0%)
    transform: translateY(0%)
  
  to 
    opacity: 0
    visibility: hidden
    -webkit-transform: translateY(10%)
    transform: translateY(10%)
  

@keyframes slideDownOut 
  from 
    opacity: 1
    visibility: visible
    -webkit-transform: translateY(0%)
    transform: translateY(0%)
  
  to 
    opacity: 0
    visibility: hidden
    -webkit-transform: translateY(10%)
    transform: translateY(10%)
  

@-webkit-keyframes slideUpIn 
  from 
    opacity: 0
    visibility: hidden
    -webkit-transform: translateY(10%)
    transform: translateY(10%)
  
  to 
    opacity: 1
    visibility: visible
    -webkit-transform: translateY(0%)
    transform: translateY(0%)
  

@keyframes slideUpIn 
  from 
    opacity: 0
    visibility: hidden
    -webkit-transform: translateY(10%)
    transform: translateY(10%)
  
  to 
    opacity: 1
    visibility: visible
    -webkit-transform: translateY(0%)
    transform: translateY(0%)
  

@-webkit-keyframes slideUpOut 
  from 
    opacity: 1
    visibility: visible
    -webkit-transform: translateY(0%)
    transform: translateY(0%)
  
  to 
    opacity: 0
    visibility: hidden
    -webkit-transform: translateY(-10%)
    transform: translateY(-10%)
  

@keyframes slideUpOut 
  from 
    opacity: 1
    visibility: visible
    -webkit-transform: translateY(0%)
    transform: translateY(0%)
  
  to 
    opacity: 0
    visibility: hidden
    -webkit-transform: translateY(-10%)
    transform: translateY(-10%)
  


